import React, { memo } from 'react'
import styled from 'styled-components'

import background from "../../assets/images/background.png";
import privateTitle from "../../assets/images/privateTitle.png";
import email from "../../assets/images/email.png";
import telegram from "../../assets/images/telegram.png";
import github from "../../assets/images/github.png";


export const AppWrapper = styled.div`
    width:100%;
    height:100vh;
    background: url(${background}) no-repeat;
    background-size: cover ;
    -webkit-background-size: cover;
    -o-background-size: cover;
    position:fixed;
    top:0;
    left:0;
    margin:0 auto;
`
export const AppTitle = styled.div`
    width:172px;
    height:32px;
    background: url(${privateTitle}) no-repeat;
    background-size: cover ;
    margin:0 auto;
    margin-top:3rem;
    margin-bottom:3rem;
    @media only screen and (max-width: 320px) {
        width:86px;
        height:16px;
        background-size: 100;
        margin-top:1rem;
        margin-bottom:1rem;
    }
`
const Community = styled.div`
    width:120px;
    display:flex;
    flex-wrap:row;
    position:absolute;
    bottom:25px;
    left:calc(50% - 60px);
`
const EmailButton = styled.a`
    margin-top:2px;
    width:21px;
    height:16px;
    background: url(${email}) no-repeat;
    background-size: cover ;
`
const TelegramButton = styled.a`
    width:20px;
    height:20px;
    background: url(${telegram}) no-repeat;
    background-size: cover ;
    margin-left:30px;
    margin-right:30px;
`
const GithubButton = styled.a`
    width:20px;
    height:20px;
    background: url(${github}) no-repeat;
    background-size: cover ;
`
const Footer = styled.div`
    color:#babcbd;
    font-size:12px;
    position:absolute;
    bottom:0px;
    left:calc(50% - 65px);
`

export default  memo(function AppBody({ children }: { children: React.ReactNode }) {
    return (
        <AppWrapper>
            <AppTitle />
            {children}
            <Community>
                <EmailButton href='mailto: pokers.finance@gmail.com' target="view_window"/>
                <TelegramButton href='https://t.me/PokerFi' target="view_window"/>
                <GithubButton href='https://github.com/pokersfinance' target="view_window"/>
            </Community>
            <Footer>© 2021 Pokers.Finance</Footer>
        </AppWrapper>
    )
})
