import React, { memo, useCallback, useEffect, useState } from 'react'
import styled from "styled-components";
import privateBox from "../../assets/images/privateBox.png";
import getPenny from "../../assets/images/getPenny.png";
import receive from "../../assets/images/receive.png";
import select_img from '../../assets/images/select.png'

import { ContractPKS, SUPPORTED_TOKENS } from '../../contracts/contracts';
import Prompt from '../../components/Prompt';
import SelectModel from '../../components/SelectModel';
import { toolFromWei } from '../../utils/tool';
import { useWallet } from 'use-wallet';

const AppBox = styled.div`
    width:300px;
    height:90vh;
    background: url(${privateBox}) no-repeat;
    background-size: 100% ;
    margin-left:5%;
    margin:0 auto;
    position:relative;
`
const BoxTitle = styled.div`
    width:60%;
    font-size:1.6rem;
    font-weight:600;
    color:#f2f2f2;
    position: absolute;;
    left:25%;
    top:2.5rem;
    display:flex;
    flex-wrap:row;
    span{
        color:#FFF3A2;
    }
    .selectStyle{
        width:18px;
        height:13px;
        margin-top:3px;
        margin-left:10px;
        background: url(${select_img}) no-repeat;
        background-size: 100% ;
    }
`
const BoxPrice = styled.div`
    width:100%;
    height:30rem;
    background: url(${getPenny}) no-repeat;
    background-size: 100% ;
    position:relative;
    top:11.5rem;
    .hold{
        width:100%;
        text-align:center;
        font-size:18px;
        color: #87B3D1;
        position:absolute;
        top:3.7rem;
    }
    .lock{
        width:100%;
        text-align:center;
        font-size:18px;
        background: linear-gradient( #FFF3A2, #FFF5D3, #FFEB8D );
        background-clip:text;
        -webkit-background-clip: text;
        color: transparent;
        position:absolute;
        top:13.7rem;
    }
    .receive{
        width:100%;
        text-align:center;
        font-size:18px;
        color: #87B3D1;
        position:absolute;
        top:22.7rem;
    }

`
const BuyButton = styled.button`
    width:40%;
    height:5rem;
    background: url(${receive}) no-repeat;
    background-size: 100% ;
    position:absolute;
    top:39rem;
    left:30%;
`
type PromptInfo = {
    status:boolean,
    message: string,
    secondaryMessage: string,
    hash:string,
}

export default memo(function Penny() {

    const [prompt,setPrompt] = useState<PromptInfo>({status:false,message:"",secondaryMessage:"",hash:""})
    const [selectModel, setSelectModel] = useState(false)
    const [token, setToken] = useState('USDT')
    const [tokenAddress, setTokenAddress] = useState('0x55d398326f99059fF775485246999027B3197955')
    const [totalReceived, setTotalReceived] = useState<string>('0')
    const [released, setReleased] = useState<string>('0')
    const [releasable, setReleasable] = useState<string>('0')

    const wallet = useWallet()
    const account = wallet.account

    useEffect(() => {
        getTotalReceived()
        getReleased()
    // eslint-disable-next-line react-hooks/exhaustive-deps
    },[])

    //总释放(发放)的代币
    const getTotalReceived = async () => {
        const data = await ContractPKS.methods.totalReceived(tokenAddress).call();
        setTotalReceived(toolFromWei(data))
    }

    //查询指定账户总释放(发放)的代币,查询指定账户可释放(发放)的代币
    const getReleased = async () => {
        const data = await ContractPKS.methods.released(tokenAddress,account).call();
        const data_1 = await ContractPKS.methods.releasable(tokenAddress,account).call();
        setReleased(toolFromWei(data))
        setReleasable(toolFromWei(data_1))
    }

    //领取
    const releaseClcik = async () =>{
        if(releasable === '0'){
            openClick(true,"提示",'没有可领取的奖励','')
        }else{
            ContractPKS.methods.release(tokenAddress,account)
            .send({from:account})
            .on('transactionHash',function(hash: any){
                openClick(true,"交易发送成功",`领取${releasable}${token}`,hash)
            })
            .on('confirmation',function(confirmationNumber: any, receipt: any){
                console.log("交易完成")
            })
            .on('receipt',function(receipt: any){
                openClick(true,"交易成功",`成功领取${releasable}${token}`,receipt.transactionHash)
                getTotalReceived()
                getReleased()
            })
            .on('error',function(error: any,receipt: any){
                openClick(true,"提示",'交易签名失败','')
            })
        }
    }

    //提示框控制
    const openClick = useCallback((status,message,secondaryMessage,hash) => {
        setPrompt({status:status,message:message,secondaryMessage:secondaryMessage,hash:hash})
        setTimeout(function(){
            setPrompt({status:false,message:"",secondaryMessage:'',hash:''})
        },4000)
    },[])

    const selectModelClick = useCallback((state,name,address) => {
        setSelectModel(state)
        if(name && address){
            setToken(name)
            setTokenAddress(address)
        }
    },[])


    return (
        <AppBox>
            <BoxTitle onClick={ () =>setSelectModel(true)}>
                选择奖励：<span>{token}</span><span className='selectStyle'></span>
            </BoxTitle>
            <BoxPrice>
                <div className='hold'> {totalReceived} {token}</div>
                <div className='lock'> {released} {token}</div>
                <div className='receive'> {releasable} {token}</div>
            </BoxPrice>
            <BuyButton onClick={releaseClcik}/>

            <SelectModel
                open={selectModel}
                openClick={selectModelClick}
                tokens={SUPPORTED_TOKENS}
            />
            <Prompt
                status={prompt.status}
                message={prompt.message}
                secondaryMessage={prompt.secondaryMessage}
                hash={prompt.hash}
            />
        </AppBox>
    )
})
