/* eslint-disable react-hooks/exhaustive-deps */
import React, { useCallback, useEffect, useState } from 'react'
import styled from 'styled-components'
import releaseBg from '../../assets/images/releaseBg.png'
import releaseBg_1 from '../../assets/images/releaseBg_1.png'
import releaseName from '../../assets/images/releaseName.png'
import selectBg from "../../assets/images/selectBg.png";
import releaseButton from "../../assets/images/releaseButton.png";
import Prompt from '../../components/Prompt'
import { ContractVEST } from '../../contracts/contracts'
import { ConversionDate, ConversionDay, toolFromWei } from '../../utils/tool'
import { useWallet } from 'use-wallet'

const AppReleaseWapper = styled.div`
    width:300px;
    height:90vh;
    background: url(${releaseBg}) no-repeat;
    background-size: 100% ;
    margin:0 auto;
    position:relative;

`

const TimeBox = styled.div`
    width:240px;
    height:80px;
    background: url(${releaseBg_1}) no-repeat;
    background-size: 100% ;
    position:absolute;
    top:60px;
    left: 20px;
    padding:10px;
`
const Box = styled.div`
    display:flex;
    justify-content:space-between;
    font-size:1.2rem;
    color:#babcbd;
    font-weight:500;
    line-height:26px;
    .yellowText{
        color:#9c925a;
    }
`

const BalanceBox =styled(TimeBox)`
    top:165px;
    left: 20px;
`
const LockTitle = styled.div`
    width:95px;
    height:24px;
    background: url(${releaseName}) no-repeat;
    background-size: 100% ;
    position:absolute;
    top:270px;
    left: 20px;
    color:#E6EFF7;
    font-size:1.2rem;
    font-weight:500;
    line-height:24px;
    padding-left:12px;
`
const ReleaseTitle = styled(LockTitle)`
    top:280px;
    left: 20px;
`
export const ReleaseBox = styled.div`
    width:90%;
    height:40px;
    background: url(${selectBg}) no-repeat;
    background-size: 100% ;
    position:absolute;
    top:310px;
    left: 5%;
    font-size: 1.4rem;
    color:#E6EFF7;
    text-align:center;
    line-height:40px;
`
const ReleaseButton = styled.button`
    width:116px;
    height:45px;
    background: url(${releaseButton}) no-repeat;
    background-size: 100% ;
    position:absolute;
    top:390px;
    left:92px;
    font-size: 1.4rem;
    color:#E6EFF7;
    padding-left:1rem;
    text-align:center;
    line-height:4rem;
`

type PromptInfo = {
    status:boolean,
    message: string,
    secondaryMessage: string,
    hash:string,
}

type OptionsInfo = {
    opening:string,
    ending: string,
    interval : number,
}

type AccountsInfo = {
    balances:string,
    released: string,
    lastReleasedTime : string,
}

export default function Release() {

    const [prompt,setPrompt] = useState<PromptInfo>({status:false,message:"",secondaryMessage:"",hash:""});
    const [options,setOptions] = useState<OptionsInfo>({opening:'',ending:'',interval: 0});
    const [Accounts,setAccounts] = useState<AccountsInfo>({balances:'',released:'',lastReleasedTime:''});
    const [releasable,setReleasable] = useState<string>('0');

    const wallet = useWallet()
    const account = wallet.account

    useEffect(() => {
        getOptions()
        getAccounts()
        getReleasable()
    }, [account])


    //查询合约参数(opening 开始释放的时间, duration 释放周期, interval 释放时间间隔)
    const getOptions = async () =>{
        const data = await ContractVEST.methods.options().call();
        const opening = ConversionDate(data.opening);
        const ending = ConversionDate(Number(data.opening) + Number(data.duration));
        const interval = ConversionDay(data.interval);
        setOptions({opening:opening,ending:ending,interval:interval});
    }

    //查询指定账户的锁仓数据(balances 余额，released 已释放金额，lastReleasedTime 最近释放时间）
    const getAccounts = async () =>{
        const data = await ContractVEST.methods.accounts(account).call();
        const balances = toolFromWei(data.balances);
        const released = toolFromWei(data.released);
        const lastReleasedTime = ConversionDate(data.lastReleasedTime);
        setAccounts({balances:balances,released:released,lastReleasedTime:lastReleasedTime});
    }

    //查询指定账户可释放的金额
    const getReleasable = async () =>{
        const data = await ContractVEST.methods.releasable(account).call();
        setReleasable(toolFromWei(data))
    }

    //释放金额
    const releaseClick = async () =>{
        if(releasable === '0'){
            openClick(true,"提示",'没有可释放的金额','')
        }else{
            ContractVEST.methods.release()
            .send({from:account})
            .on('transactionHash',function(hash: any){
                openClick(true,"交易发送成功",`释放${releasable}PK`,hash)
            })
            .on('confirmation',function(confirmationNumber: any, receipt: any){
                console.log("交易完成")
            })
            .on('receipt',function(receipt: any){
                openClick(true,"交易成功",`成功释放${releasable}PK`,receipt.transactionHash)
                getAccounts()
                getReleasable()
            })
            .on('error',function(error: any){
                openClick(true,"提示",'交易签名失败','')
                console.log(error)
            })
        }
    }

    //提示框控制
    const openClick = useCallback((status,message,secondaryMessage,hash) => {
        setPrompt({status:status,message:message,secondaryMessage:secondaryMessage,hash:hash})
        setTimeout(function(){
            setPrompt({status:false,message:"",secondaryMessage:'',hash:''})
        },4000)
    },[])

    return (
        <AppReleaseWapper>
            <TimeBox>
                <Box>
                    <span>开始释放时间：</span>
                    <span>{options.opening}</span>
                </Box>
                <Box>
                    <span>结束释放时间：</span>
                    <span>{options.ending}</span>
                </Box>
                <Box>
                    <span>间隔释放周期：</span>
                    <span className='yellowText'>{options.interval} 天</span>
                </Box>
            </TimeBox>
            <BalanceBox>
                <Box>
                    <span>剩余锁仓金额：</span>
                    <span className='yellowText'>{Accounts.balances} PK</span>
                </Box>
                <Box>
                    <span>已释放金额：</span>
                    <span>{Accounts.released} PK</span>
                </Box>
                <Box>
                    <span>最近释放时间：</span>
                    <span>{Accounts.lastReleasedTime}</span>
                </Box>
            </BalanceBox>
            <ReleaseTitle>可释放金额</ReleaseTitle>
            <ReleaseBox>{releasable} PK</ReleaseBox>
            <ReleaseButton onClick={releaseClick}/>
            <Prompt
                status={prompt.status}
                message={prompt.message}
                secondaryMessage={prompt.secondaryMessage}
                hash={prompt.hash}
            />
        </AppReleaseWapper>
    )
}
