import React, { memo, useEffect } from 'react';
import { Redirect, Route, Switch } from 'react-router-dom';
import styled from 'styled-components';
import loginTitle_img from "./assets/images/loginTitle.png";
import metaMask_img from "./assets/images/metaMask.png";
import walletConnect_img from "./assets/images/walletConnect.png";
import AppBody, { AppTitle } from './components/AppBody';

import Whitelist from './pages/whitelist';
import Penny from './pages/penny';
import Release from './pages/release';
import Buy from './pages/buy';
import Address from './pages/address';

import { useWallet } from 'use-wallet'

import web3 from './contracts/initWeb3';

const LoginTitle = styled(AppTitle)`
    width:160.5px;
    height:32px;
    background: url(${loginTitle_img}) no-repeat;
    background-size: 100% ;
    margin-top:7rem;
`

const LoginButton = styled.button`
    width:162.5px;
    height:61.5px;
    background: url(${walletConnect_img}) no-repeat;
    background-size: 100% ;
    position:absolute;
    top:50vh;
    left:calc(50% - 81.25px);
`

const MetamaskButton = styled(LoginButton)`
    top:37vh;
    background: url(${metaMask_img}) no-repeat;
    background-size: 100% ;
`

declare const window: any;


export default memo(function App() {

    const wallet = useWallet();
    const activate = (connector: "injected" | "walletconnect" ) => wallet.connect(connector)
    useEffect(() => {
        getAppInfo()
    // eslint-disable-next-line react-hooks/exhaustive-deps
    }, []);

    const getAppInfo = async () => {
        if (/(iPhone|iPad|iPod|iOS)/i.test(navigator.userAgent)) {
            activate('injected')
        } else if (/(Android)/i.test(navigator.userAgent)) {
            activate('injected')
        } else {
            const address = await web3.eth.getAccounts();
            const wallet = localStorage.getItem('walletconnect');
            if(address.length > 0){
                activate('injected')
            }else if(wallet){
                activate('walletconnect')
            }
        };
    }


    const injectedClick = async () =>{
        try {
            const chainId = window.ethereum.networkVersion;
            // eslint-disable-next-line eqeqeq
            if(chainId == wallet.chainId){
                activate('injected')
            }else{
                alert("请在 Metamask 中将网络设置为：Binance Smart Chain，或选择为BSC钱包。")
            }
        } catch (error) {
            alert("请在安装MetaMask!")
        }
    }

    const walletconnectClick = async () =>{
        try {
            activate('walletconnect')
        } catch (error) {
            console.log(error)
        }

    }

  return (
    <AppBody>
        {
            wallet.account ?
            (
                <Switch>
                    <Route exact={true} path="/whitelist" component={Whitelist}/>
                    <Route path="/penny" component={Penny}/>
                    <Route path="/release" component={Release}/>
                    <Route path="/buy" component={Buy}/>
                    <Route path="/address" component={Address}/>
                    <Redirect to="/release" />
                </Switch>
            ):(
                <>
                    <LoginTitle />
                    <MetamaskButton onClick={injectedClick}/>
                    <LoginButton onClick={walletconnectClick}/>
                </>
            )
        }
    </AppBody>
  );
})
